.aboutbanner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 97%;
    height: 210px;
    border-radius: 25px;
    background-position: 25% 50%;
    background-size: cover;
    
  }
  
  .aboutbanner-text {
    color: #ffffff;
    filter: brightness(100%);
    font-size: 48px;
    font-weight: 700;
    text-shadow: 2px 2px 10px black;
  }
  
  @media screen and (max-width: 768px) {
    .banner {
      height: 125px;
    }
  
    .banner-about {
      height: 250px;
    }
  
    .banner-text {
      text-align: center;
      font-family: Montserrat;
      font-size: 22px;
      font-weight: 700;
    }
  }
  